@import url(https://fonts.googleapis.com/css?family=Exo+2:200i);
html {
  background-image: url(/guyleifer.github.io/loop-machine/static/media/background.9c42ba0a.jfif);
  height: 100%;
  margin: 0px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.App {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.loop-machine {
    margin: 0 auto;
    /* max-height: 500px; */
    width: 80vw;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

@media only screen and (min-width: 1050px) and (min-height: 550px) {
    .container {
        -webkit-flex-direction: row;
                flex-direction: row;
    }
}

