.beat {
  position: relative;
  width: 30%;
  margin: 30px auto;
  border-radius: 10%;
  cursor: pointer;
}

.miniBeatStyle {
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
  /* margin: 10px; */
}

.icon {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

