.controllerStyle {
    right: 10%;
    margin: 10%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.iconStyle {
    position: absolute;
    top: 5px;
    left: 5px;
    height: 50px;
    width: 50px;
    cursor: pointer;
}

.iconsDivStyle {
    position: relative;
    height: 60px;
    width: 60px;
    margin: 10px;
    background-color: white;
    border-radius: 50%;
}

.iconsDivDisabledStyle {
    position: relative;
    height: 60px;
    width: 60px;
    margin: 10px;
    background-color: grey;
    border-radius: 50%;
}

.iconsDivRecordStyle {
    position: relative;
    height: 60px;
    width: 60px;
    margin: 10px;
    background-color: red;
    border-radius: 50%;
}

@media only screen and (max-width: 510px) {
    .iconsDivStyle {
        margin: 3% auto;
        height: 50px;
        width: 50px;
    }

    .iconsDivDisabledStyle {
        margin: 3% auto;
        height: 50px;
        width: 50px;
    }

    .iconsDivRecordStyle {
        margin: 3% auto;
        height: 50px;
        width: 50px;
    }

    .iconStyle {
        position: absolute;
        top: 3px;
        left: 3px;
        height: 44px;
        width: 44px;
        cursor: pointer;
    }

    .controllerStyle {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media only screen and (min-width: 1050px) {
    .controllerStyle {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
    }
}
.header {
    font-family: 'Exo 2', sans-serif;
    font-size: 400%;
    text-align: center;
    color: #eff;
    text-shadow: 3px 3px 3px rgb(131, 247, 247);
    margin: 5% auto 10% auto;
}

@media only screen and (min-width: 1100px) {
    .header {
        font-size: 600%;
        margin-bottom: 5%;
    }
}
